s compuestos químicos diatómicos y poliatómicos. La ilustración de tales fuerzas atractivas es un área compleja que está definida por las leyes de la química cuántica.")]),_c('LineaTiempoD',{staticClass:"mb-5"},[_c('div',{staticClass:"row",attrs:{"numero":"1","titulo":"Enlace iónico"}},[_c('p',[_vm._v("Enlace iónico (o enlazamiento iónico) se define como el efecto de transferencia de electrones (o paso de electrones) de un átomo a otro. El enlace iónico se genera entre un átomo electropositivo y uno electronegativo. El átomo electropositivo concede sus electrones y el átomo electronegativo los admite, como consecuencia de este proceso se aparecen iones positivos (con valencia +n1) y negativos (con valencia - n2) con configuraciones de capa sellada. En estas circunstancias, los iones con cargas +n1 y - n2 perciben atracción mutua. La fuerza de repulsión se revela cuando las configuraciones electrónicas de capa sellada iónica inician a traslaparse.")])]),_c('div',{staticClass:"row",attrs:{"numero":"2","titulo":"Enlace covalente"}},[_c('p',[_vm._v("El nombre “covalente” nace de la colocación compartida, cooperativa, de electrones de valencia entre dos átomos contiguos. Una peculiaridad significativa de estos enlaces es que se logran crear entre átomos del mismo tipo, entre los cuales puede haber muy poca o ninguna formación de enlaces iónicos, el enlace covalente se forma entre átomos con pequeñas diferencias de electronegatividad y situados muy próximos en la tabla periódica, habitualmente se comparten sus electrones externos s y p con otros átomos, de modo que consigue la configuración electrónica de gas noble.")]),_c('p',[_vm._v("En un enlace covalente sencillo, cada uno de los dos átomos contribuye con un electrón a la formación del par de electrones del enlace, y las energías de los dos átomos agrupados con el enlace covalente son menores (más estables) como resultado de la interacción de los electrones. En el enlace covalente, se logran formar múltiples pares de 