s moléculas compuestas de dos o más átomos desiguales, pueden crear dipolos permanentes entre los átomos en claridad de sus diferencias de electronegatividad. El átomo más electronegativo atrae el electrón hacia sí y lo aleja del elemento menos electronegativo; se crea una molécula polar con dipolo permanente, en la que el segundo átomo tiene carga positiva y el primero, carga negativa. ")])])])],1)])],1),_c('Separador'),_vm._m(7),_c('p',[_vm._v("La resistencia en los materiales varía considerablemente de acuerdo a su clase. El oro y la plata son los materiales que menos oposición presentan al paso de una corriente eléctrica; en cambio hay otros materiales como el silicio que presenta una resistencia mucho mayor a la que pueden presentar materiales como el cobre o el aluminio. Finalmente existen otros materiales que presentan una altísima resistencia al paso de la corriente eléctrica.")]),_c('h3',{staticClass:"mb-5"},[_vm._v("Teniendo en cuenta esta variación, los materiales se clasifican así:")]),_c('TabsA',{staticClass:"color-primario mb-5"},[_c('div',{staticClass:"tarjeta color-primario--borde p-4",attrs:{"titulo":"Materiales conductores"}},[_c('h4',[_vm._v("Materiales conductores")]),_c('p',[_vm._v("Son aquellos que están constituidos por elementos que presentan una bajísima oposición al paso de los electrones debido a que los átomos que los conforman poseen gran cantidad de electrones libres. Por ejemplo: plata, cobre, aluminio, tungsteno, cinc, entre otros.")]),_c('div',{staticClass:"col-lg-5 mx-auto"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('p',{staticClass:"titulo pb-0 mb-0"},[_c('b',[_vm._v("Figura 2. ")]),_vm._v(" "),_c('i',[_vm._v("Electrones atravesando un conductor.")])])]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t2/f7.svg"),"alt":"Electrones atravesando un conductor"}})])])]),_c('div',{staticClass:"tarjeta color-primario--borde p-4",attrs:{"titulo":"Elementos de materiales conductores"}},[_c('h4',[_vm._v("E